@use "sass:map";
@use "../common/spacing";
// --------------------------------------------------
// Grid variables
// --------------------------------------------------

// Grid
// -------------------------
$columns: 12 !default; // number of columns
$row-max-width: 100% !default; // max-width of row

// Breakpoints
// -------------------------

$breakpoints: ();
//$breakpoints: (
//  xs: 480px,
//  sm: 768px,
//  md: 992px,
//  lg: 1200px,
//  xlg: 1600px
//);

// Spacing
// -------------------------
$col-spacing: 0 !default;

$gap-sizes: (
    zero: map.get(spacing.$spacing-list, "zero"),
    s: map.get(spacing.$spacing-list, "s"),
    m: map.get(spacing.$spacing-list, "m"),
    l: map.get(spacing.$spacing-list, "l"),
);
