@use "sass:math";
@use "sass:map";

@mixin columns-common-width() {
    flex: 0 0 100%;
    max-width: 100%;
}
// --------------------------------------------------
// Row and columns generation mixins
// --------------------------------------------------

@function calculate-width-percentage($columns-count, $columns-total) {
    @return math.percentage(math.div($columns-count, $columns-total));
}

@mixin generate-columns($columns, $breakpoint: null) {
    @for $i from 1 through $columns {

        &.-#{$breakpoint}col-#{$i} {
            $width: calculate-width-percentage($i, $columns);
            flex: 0 0 $width;
            max-width: $width;
        }
    }
}

@mixin generate-columns-vertical-gap($gap-size) {
    $editor-tags: 'sf-add-widget, sf-fake-widget';
    $widget-tags: 'sf-system-status';
    & > :not(#{$editor-tags}) {
        margin-top: $gap-size;
    }

    & > :first-child:not(#{$editor-tags}),
        :is(#{$widget-tags}):empty {
        margin-top: 0;
    }


    & > :first-child:is(#{$editor-tags}),
        :first-child:is(#{$widget-tags}):empty {
        margin-bottom: calc(-1 * $gap-size);
    }
}

@mixin generate-columns-with-gap($columns, $gap-portion, $gap-size) {
    @for $i from 1 through $columns {

        &.-col-#{$i} {
            $width: calculate-width-percentage($i, $columns);
            $gap-width: $width;

            @if $gap-portion > 0 {
                $gap-width:  calc($width - $gap-portion);
            }

            flex: 0 0 $gap-width;
            max-width: $gap-width;
        }
    }

    @include generate-columns-vertical-gap($gap-size);
}

@mixin generate-section-with-gap($gap-sizes, $columns, $selector-prefix, $size: "m") {
    $gap-size: map.get($gap-sizes, $size);
    column-gap: $gap-size;

    @for $i from 0 through $columns {
        &[data-sfcolumn-count="#{$i}"] {
            $column-count: $i;

            @if $column-count > 1 {
                $gap-count: $column-count - 1;
                $gap-portion: 0;

                @if $gap-size > 0 {
                    $gap-portion: calc($gap-count * $gap-size / $column-count);
                }

                & > .#{$selector-prefix}-row__col {
                    @include generate-columns-with-gap($columns, $gap-portion, $gap-size);
                }
            }
        }
    }
}

@mixin generate-columns-fallback($columns, $breakpoint) {
    @for $i from 1 through $columns {

        &.-#{$breakpoint}col-#{$i} {
            @include columns-common-width;
        }
    }
}

@mixin generate-responsive-columns($breakpoints, $columns) {
    @each $breakpoint, $value in $breakpoints {
        @media only screen and (min-width: $value) {
            @include generate-columns($columns, "#{$breakpoint}-");
        }

        @include generate-columns-fallback($columns, "#{$breakpoint}-");
    }
}

// --------------------------------------------------
// Column order class generation mixins
// --------------------------------------------------

@mixin generate-order-helpers($columns, $breakpoint: null) {
    @for $i from 1 through $columns {

        &.-#{$breakpoint}order-#{$i} {
            order: $i;
        }
    }
}

@mixin generate-responsive-order-helpers($breakpoints, $columns) {
    @each $breakpoint, $value in $breakpoints {
        @media only screen and (min-width: $value) {
            @include generate-order-helpers($columns, "#{$breakpoint}-");
        }
    }
}

// --------------------------------------------------
// Column hidden class generation mixins
// --------------------------------------------------

@mixin generate-responsive-hidden-helpers($breakpoints) {
    @each $bp in map-keys($breakpoints) {
        &.-#{$bp}-hidden-up {
            @include media-breakpoint-up($bp) {
                display: none !important;
            }
        }
        &.-#{$bp}-hidden-down {
            @include media-breakpoint-down($bp) {
                display: none !important;
            }
        }
    }
}

@function is-breakpoint-first($name, $breakpoints: $breakpoints) {
    $n: list.index(map.keys($breakpoints), $name);

    @return if($n == 1, true, null);
}

@function is-breakpoint-last($name, $breakpoints: $breakpoints) {
    $n: list.index(map.keys($breakpoints), $name);

    @return if($n == length($breakpoints), true, null);
}

@mixin media-breakpoint-up($name, $breakpoints: $breakpoints) {
    $first: is-breakpoint-first($name, $breakpoints);

    @if not $first {
        @media (min-width: map-get($breakpoints, $name)) {
            @content;
        }
    }

    @else {
        @content;
    }
}

@mixin media-breakpoint-down($name, $breakpoints: $breakpoints) {
    $last: is-breakpoint-last($name, $breakpoints);

    @if not $last {
        @media (max-width: (map-get($breakpoints, $name) - 1px)) {
            @content;
        }
    }

    @else {
        @content;
    }
}
