$tb-typography: (
    kendo-default-typography: (font-family: '"Segoe UI", -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif',
        font-size: 14px,
        font-weight: 400,
        line-height: 1.4285714286,
    ),
);

@mixin typography-classes ($typography) {

    @each $selector,
    $property in $typography {
        &-#{$selector} {

            @each $propName,
            $propValue in $property {
                #{$propName}: #{$propValue};
            }
        }
    }
}

$tb-effects: (
    kendo-box-shadow-depth-1: (box-shadow: (0px 1.6px 3.6px 0 #00000022, 0px 0.3px 0.9px 0 #0000001c), ),
    kendo-box-shadow-depth-2: (box-shadow: (0px 3.2px 7.2px 0 #00000022, 0px 0.6px 1.8px 0 #0000001c), ),
    kendo-box-shadow-depth-3: (box-shadow: (0px 6.4px 14.4px 0 #00000022, 0px 1.2px 3.6px 0 #0000001c), ),
    kendo-box-shadow-depth-4: (box-shadow: (0px 25.6px 57.6px 0 #00000038, 0px 4.8px 14.4px 0 #0000002e), ),
    tb-internal-none-effects: (box-shadow: (none), filter: (none), backdrop-filter: (none), ),
);

@mixin effects-classes ($effects) {

    @each $selector,
    $property in $effects {
        &-#{$selector} {

            @each $propName,
            $propValue in $property {
                #{$propName}: $propValue;
            }
        }
    }
}
