@use "sass:meta";
@use "../sitefinity/framework";

@import "@progress/kendo-theme-default/scss/datetimepicker";
@import "@progress/kendo-theme-default/scss/colorpicker";
@import "@progress/kendo-theme-default/scss/dropdownlist";
@import "@progress/kendo-theme-default/scss/editor";
@import "@progress/kendo-theme-default/scss/tabstrip";
@import "@progress/kendo-theme-default/scss/pager";

// Kendo Theme builder overrides
@include meta.load-css("./theme-builder/overrides");

//#region Styles needed for Admin app in Sitefinity
.-sf-integrated-app {
    .k-widget:not(.k-scheduler),
    .k-datepicker,
    .k-timepicker {
        &::selection,
        ::selection {
            color: framework.$color-primary-invert;
            background: framework.$color-info-bg;
        }
    }

    .k-widget:not(.k-scheduler) {
        .k-input,
        .k-editor-toolbar .k-dropdown,
        .k-colorpicker .k-picker-wrap {
            font-size: framework.$input-font-size;
        }

        .k-input,
        .k-textbox {
            height: framework.$input-height;
            text-indent: 0;
        }

        textarea.k-input,
        textarea.k-textbox {
            min-height: framework.$textarea-min-height;
            padding: framework.$textarea-padding;
        }

        .k-button {
            &:active,
            &.k-active,
            &.k-selected,
            &.k-state-active,
            &.k-state-selected {
                box-shadow: none;
                background: none;
            }

            &.k-primary,
            &.k-primary:active {
                background-image: none;
            }
        }
    }

    .k-overlay {
        background-color: framework.$color-primary;
        opacity: 1 !important;
    }

    .k-dropdown .k-state-focused {
        color: framework.$color-primary-invert;
        background-color: transparent;
        box-shadow: none;
    }

    .k-popup {
        background-color: framework.$color-primary;
    }
}
//#endregion

// Page editior in integration mode  overrides
$selector-app: ".k-widget, .k-popup, .k-animation-container";

.-sf-modal-window-open {

    #{$selector-app} {
        --font-size-h1: 42px;
        --font-size-h4-lg: 14px;
        --font-size-lg: 15px;
        --icon-size-xs: 14px;
        --icon-size-s: 16px;
        --icon-size-m: 18px;
    }
}

//#region Kendo pager
.k-pager {
    &:focus,
    &.k-focus {
        box-shadow: none;
    }

    .k-pager-numbers-wrap .k-pager-numbers .k-button {
        align-items: center;
        justify-content: center;
        border: 1px solid transparent;

        &::before,
        &::after {
            display: none !important;
        }

        &.k-selected {
            background-color: #eee !important;
            background-image: none !important;
        }
    }
}
//#endregion
